import { addTabPage } from '@/utils/route';

// 驾驶舱专用大屏穿透路由跳转（带设置的开始结束时间）
export const handleRouteChange = (url: string, extraParams: Object) => {
  if (sessionStorage.getItem('screen-date-set')) {
    const params = JSON.parse(sessionStorage.getItem('screen-date-set') as string);
    if (Object.keys(extraParams).length) {
      Object.assign(params, extraParams);
    }
    addTabPage(url, '', params);
  } else {
    console.info('no screen-data-set and can not turn into new page.');
  }
};
